import React, { useState } from "react";
import { Button } from "@/components/ui/button"; // shadcn button
import Link from "next/link";

const Menu = () => {
    const [theme, setTheme] = useState("light");

    const toggleTheme = () => {
        const newTheme = theme === "light" ? "dark" : "light";
        setTheme(newTheme);
        document.documentElement.setAttribute("data-theme", newTheme);
    };

    return (
        <header className="fixed top-0 left-0 w-full bg-transparent backdrop-blur-md shadow-lg z-50">
            <div className="container mx-auto px-6 py-4 flex justify-between items-center">
                {/* Logo */}
                <Link href="/" className="text-xl font-bold text-white">
                    MyBrand
                </Link>

                {/* Navigation Links */}
                <nav className="hidden md:flex space-x-6">
                    <Link href="/" className="text-white hover:text-gray-300 transition">Home</Link>
                    <Link href="/about" className="text-white hover:text-gray-300 transition">About</Link>
                    <Link href="/services" className="text-white hover:text-gray-300 transition">Services</Link>
                    <Link href="/contact" className="text-white hover:text-gray-300 transition">Contact</Link>
                </nav>

                {/* Theme Switcher */}
                <Button onClick={toggleTheme} className="hidden md:inline-block border-white text-white hover:bg-white hover:text-black">
                    {theme === "light" ? "Dark Mode" : "Light Mode"}
                </Button>

                {/* Call to Action Button */}
                <Button variant="outline" className="hidden md:inline-block border-white text-white hover:bg-white hover:text-black">
                    Get Started
                </Button>
            </div>
        </header>
    );
};

export default Menu;
